<template>
    <div :class="`liquidity-provider-pool ${getColor(pool.risk)}`">
        <div class="liquidity-provider-pool-cell cell-1">
            <div class="images-block">
                <img class="image image-1" :src="require(`@/assets/newImages/coins/vectors/${pool.leftCoinTicker}.svg`)">
                <img class="image image-2" :src="require(`@/assets/newImages/coins/vectors/${pool.rightCoinTicker}.svg`)">
            </div>

            <div class="text-block">
                <div class="title">
                    {{$localizeFilter('coins_pair')}}
                </div>
                <div class="text bold">
                    {{`${pool.leftCoinTicker} / ${pool.rightCoinTicker}`}}
                </div>
            </div>
        </div>

        <div class="liquidity-provider-pool-cell cell-2">
            <div class="text-block">
                <div class="title">
                     {{$localizeFilter('interest')}}
                </div>
                <div class="text">
                    {{`${pool.interest} %`}}
                </div>
            </div>
        </div>

        <div class="liquidity-provider-pool-cell cell-3">
            <div class="text-block">
                <div class="title">
                    {{$localizeFilter('risk')}}
                </div>
                <div class="text">
                    {{`${pool.risk}%`}}
                </div>
            </div>
        </div>

        <div class="liquidity-provider-pool-cell cell-4">
            <div class="text-block">
                <div class="title">
                    {{$localizeFilter('deposited')}}
                </div>
                <div class="text">
                    {{`${pool.depositedLeftCoin}&nbsp;${pool.leftCoinTicker}&nbsp;/ ${pool.depositedRightCoin}&nbsp;${pool.rightCoinTicker}`}}
                </div>
            </div>
        </div>


        <div class="liquidity-provider-pool-cell cell-5">
            <div class="buttons-block">
                <div class="basic-button" @click="deposit">{{$localizeFilter('liquidity_deposit')}}</div>
                <div class="underline-button" @click="withdraw">{{$localizeFilter('liquidity_withdraw')}}</div>
            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'

export default defineComponent({
    props: {
        pool: {
            type: Object,
            required: true
        }
    },
    watch: { 
    },
    data: ()=> ({
    }),
    methods: {
        deposit() {
            this.$emit('deposit', this.pool)
        },
        withdraw() {
            this.$emit('withdraw', this.pool)
        },
        getColor(risk) {
            if (risk < 2) { return 'risk-green' }
            else if (risk < 3.5) { return 'risk-yellow' }
            else if (risk < 5) { return 'risk-orange' }
            else { return 'risk-red' }
        }
    },
})
</script>

