<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal">
        <div class="new-modal liquidity-deposit-modal" @click.stop="">
            <div class="new-modal-back-button-container">
                <button class="new-modal-back-button" @click="closeModal"></button>
            </div>
            <div class="modal-content">
        <p class="window-title">{{$localizeFilter('withdrawing_funds_from_pool') + `\n${pool.leftCoinTicker}/${pool.rightCoinTicker}`}}</p>

        <p class="window-subtitle">{{$localizeFilter('liquidity_withdraw_subtitle', [pool.depositedLeftCoin, pool.leftCoinTicker, pool.depositedRightCoin, pool.rightCoinTicker])}}</p>
        
        <div class="modal-warning">
            <i class="material-icons" style="color: #FF9400!important;">report</i>
            <p class="window-text warning">{{$localizeFilter('liquidity_withdraw_warning')}}</p>
        </div>

        <div class="conditions-block" @click="acceptСonditions">
             <input type="checkbox" id="checkbox" class="checkbox" v-model="conditionsAccepted" @click="acceptСonditions">
             <div class="checkbox-labels-block">
                <label for="checkbox" class="conditions-label">{{$localizeFilter('liquidity_withdraw_agreement')}}</label>
             </div>
         </div>


        <!-- :class="conditionsAccepted ? '' : 'inactive'" -->
        <div class="buttons-block">
            <button class="transparent-button" @click="submitHandler">{{$localizeFilter('confirm')}}</button>
            <button class="basic-button red-button" @click="cancel">{{$localizeFilter('cancel')}}</button>
        </div>

    </div>
    </div>
    </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import localizeFilter from '@/filters/localize.filter'

function multiplyNumberByDecimals(value, decimalsNumber) {
    // const decimals = decimalsNumber.toString().length
    let [integer, fraction] = value.toString().split('.')
    // console.log(decimalsNumber)
    let res = 0n;
    if(fraction) {
        res += BigInt(fraction) * decimalsNumber;
        for( let i = 0; i < fraction.length; i++ ) {
            res /= BigInt(10);
        }
    }
    res += BigInt(integer) * decimalsNumber;
    return res;
}

export default defineComponent({
    props: {
        pool: {
            type: Object,
            required: true
        }
    },
    data: () => ({
        conditionsAccepted: false
    }),
    methods: {
        acceptСonditions() {
            this.conditionsAccepted = !this.conditionsAccepted
        },
        closeModal(reload) {
            this.$emit("closeModal", reload);
        },
        cancel() {
            this.closeModal()
        },
        async submitHandler() {
            if (!this.conditionsAccepted) {
                return;
            }

            // send data to store
            try {
                const data = {poolId: this.pool.poolId}
                await this.$store.dispatch('withdrawFundsFromLiquidityPool', data)
                this.$message(localizeFilter('withdraw_funds_from_pool_application_created_successful'))
                this.$emit('closeModal')
            } catch(e) {
                this.$error(e)
            } 
        },
    },
    computed: {
        assetDecimalsMap() {
            return this.$store.getters.assetDecimalsMap;
        },
    }
})
</script>


